import React, { Component, useRef, useState } from 'react';
import '../../node_modules/fundamental-styles/dist/fundamental-styles.css';
import {
  addInitListener,
  addContextUpdateListener,
  removeContextUpdateListener,
  removeInitListener
} from '@luigi-project/client';
import {
  FlexBox,
  FlexBoxAlignItems,
  FlexBoxDirection,
  FlexBoxJustifyContent,
  ShellBar,
  ThemeProvider,
  Popover,
  PlacementType,
  Card,
  Text,
  List,
  ValueState,
  AnalyticalTable,
  ProgressIndicator,
  Title,
  TitleLevel,
  StandardListItem,
  Icon
} from '@ui5/webcomponents-react';

import { spacing } from '@ui5/webcomponents-react-base'
// import {LineChart} from '@ui5/webcomponents-react-charts/lib/next/LineChart'
import { LineChart, BarChart } from '@ui5/webcomponents-react-charts'

import '@ui5/webcomponents-icons/dist/icons/line-chart.js';
import '@ui5/webcomponents-icons/dist/icons/horizontal-bar-chart.js';
import "@ui5/webcomponents-icons/dist/icons/list.js";
import "@ui5/webcomponents-icons/dist/icons/table-view.js";


const [toggleChart, setToggleChart] = useState("lineChart");
const [loading, setLoading] = useState(false);
const popoverRef = useRef();
const onProfileClick = (event) => {
  popoverRef.current.openBy(event.detail.targetRef);
}
const handleHeaderClick = (event) => {
  if (toggleChart === "lineChart") {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
      setToggleChart("barChart");
    }, 200);
  } else {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
      setToggleChart("lineChart");
    }, 200);
  }
}
const datasets = [{
  label: "Stock Pric",
  data: [65, 59, 80, 81, 56, 55, 40]
}];
const labels = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July"
];
const tableData = new Array(500).fill(null).map((_, index) => {
  return {
    name: `name${index}`,
    age: Math.floor(Math.random() * 100),
    friend: {
      name: `friend.Name${index}`,
      age: Math.floor(Math.random() * 100)
    }
  };
});

const tableColumns = [
  {
    Header: "Name",
    accessor: "name" // String-based value accessors!
  },
  {
    Header: "Age",
    accessor: "age"
  },
  {
    Header: "Friend Name",
    accessor: "friend.name"
  },
  {
    Header: "Friend Age",
    accessor: "friend.age"
  }
];
export default class DashBoard extends Component {

  constructor(props) {
    super(props);
    this.state = {
      message: 'in constructor'
    };
    this.name = "karthik";
    console.log("in construc");
    this.initListener = null;
    this.contextUpdateListener = null;

  }
  componentDidMount() {
    this.initListener = addInitListener(initialContext => {
      this.setState({
        message: 'Luigi Client initialized.'
      });
      console.log("in init ka");
    });
    this.contextUpdateListener = addContextUpdateListener(updatedContext => {
      this.setState({
        message: 'Luigi Client updated.'
      });
    });
  }

  componentWillUnmount() {
    removeContextUpdateListener(this.contextUpdateListener);
    removeInitListener(this.initListener);
  }

  render() {
    return (
      <div>
        <section className="fd-section">
          <div className="fd-section__header">
            <h1 className="fd-section__title">DashBoard</h1>
          </div>
          <Card id="card1" heading="test" style={{ width: "300px", ...spacing.sapUiContentPadding }}
            headerInteractive
            avatar={<Icon name={toggleChart === "lineChart" ? "line-chart" : "horizontal-bar-chart"} />}
            onHeaderClick={handleHeaderClick}>
            <Text style={spacing.sapUiContentPadding}> Card content </Text>
            {
              toggleChart === "lineChart" ?
                <LineChart datasets={datasets} labels={labels} loading={loading} /> :
                <BarChart datasets={datasets} labels={labels} loading={loading} />
            }
          </Card>
          <Card id="card1" heading="list" style={{ width: "300px", ...spacing.sapUiContentPadding }}
            avatar={<Icon name="list" />}>
            <Text style={spacing.sapUiContentPadding}> Card content </Text>
            <StandardListItem info="finished" infoState={ValueState.Success}>
              Activity 1
        </StandardListItem>
            <StandardListItem info="failed" infoState={ValueState.Error}>
              Activity 2
        </StandardListItem>
            <StandardListItem
              info="in progress"
              infoState={ValueState.Warning}
              style={{ height: "80px" }}>
              <FlexBox direction={FlexBoxDirection.Column}>
                <Title level={TitleLevel.H5}>Activity 3</Title>
                <ProgressIndicator
                  displayValue="89%"
                  percentValue={89}
                  width="180px"
                  state={ValueState.Success} />
              </FlexBox>
            </StandardListItem>
            <StandardListItem
              info="in progress"
              infoState={ValueState.Warning}
              style={{ height: "80px" }}>
              <FlexBox direction={FlexBoxDirection.Column}>
                <Title level={TitleLevel.H5}>Activity 4</Title>
                <ProgressIndicator
                  displayValue="5%"
                  percentValue={5}
                  width="180px"
                  state={ValueState.Error} />
              </FlexBox>
            </StandardListItem>
          </Card>
          <Card heading="AnalyticalTable" style={{ maxWidth: "900px", ...spacing.sapUiContentPadding }} avatar={<Icon name="table-view" />}>
            <AnalyticalTable
              data={tableData}
              columns={tableColumns}
              visibleRows={5} />
          </Card>
        </section>
      </div>
    );
  }
}
